import React from 'react';
import {Panel, Div, Button, Cell, Avatar} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import VKStories from "vk-stories";
import connect from "@vkontakte/vkui-connect-promise";
VKStories.init(connect);




//компонент модального окна
class Modal extends React.Component {
  constructor(props) {
      super(props);
  }



     

   

  render() {
    return(
      <Div align="center">
       <Avatar src={this.props.fetchedUser.photo_200} size={150} />
       <Cell id="name">{this.props.fetchedUser.first_name+" "+this.props.fetchedUser.last_name}</Cell>
      <Button size="xl" onClick={this.props.GenerateStory}>Поделиться</Button>
      </Div>
   );
  }

}

export default Modal;